
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
Masker
},
data () {
return {
isKnow: false
}
},
mounted () {
},
computed: {
},
methods: {
signContract () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/signContract.do'
let data = {
action: 'init_request',
path: path,
params: self.$store.state.signInfo
}
self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success') {
this.isKnow = true
this.$router.replace('/')
this.$store.state.tabItem = 0
}
})
},
giveUpSisgnContract () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/giveUpSignContract.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token,
applyId: self.$store.state.applyNo, // 申请编号
image: self.$store.state.faceId // 人脸照片
}
}
self.$store.dispatch('normalRequest', data).then(res => {
})
}
}
}



